@import url(https://fonts.googleapis.com/css2?family=Archivo&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@200;300;400;500;600;700&display=swap);
body {
    margin: 0;
    width: 100%;
    height: 100vh;
    -ms-scroll-chaining: none;
        overscroll-behavior: none;
    background-color: #222;
}
#demo-simple-select-outlined-label{
}
#select{
    color:#f5f5f5;
    background-color:#333;
}
#label{
    color:#f5f5f5;
    font-size:.9em;
}
#picker{
    text-align:center;
}
/* shared css */
.chart-wrapper{
    display:flex;
    background-color:#333;
    margin-bottom:1em;
    justify-content:space-evenly;
    align-items:center;
}
.chart{
    width:385px;
    display:flex;
    flex-direction:column;
    align-items:center;
    margin-top:1em;
}
.chart-header{
    font-size:.7em;
    margin-top:1em;
    position:relative;
    top:25px;
    left:80px;
    text-align:center;
}
.chart-toggler{
    display:flex;
    justify-content:space-between;
    align-items:center;
    margin:0 2em;
}

/* global chart */
#global-chart-wrapper{
    background-color:#333;
    border: 1px solid#444;
    max-width:100%;
    position:relative;
    display:flex;
    flex-direction:column;
    align-items:center;
}
.chart-wrapper-global{
    padding-bottom:2px;
}
#line-chart{
    border-right:1px solid #f5f5f5;
}
#line-chart, #bar-chart{
    margin-top:-1em;
}
.global-chart{
    margin-top:3.5em;
}
.global-header{
    position:relative;
    font-size:.7em;
    left:90px;
}
/* country chart */
.country-header{
    position:relative;
    font-size:.7em;
    left:60px;
    top:10px;
}
#bar-chart{
    margin-bottom:2em;
}
/* global today chart */
.daily-header{
    position:relative;
    font-size:.7em;
    left:130px;
    top:10px;
}
.infected-active-chart{
    border-right:1px solid #f5f5f5;
    margin-top:-1em;
}
.deaths-recovered-chart{
    margin-top:-1em;
}
.loader-country-chart-wrapper{
    display:flex;
    flex-direction:column;
    align-items:center;
    margin-top:10%;
}
.loader-global-chart-wrapper{
    display:flex;
    flex-direction:column;
    align-items:center;
    margin-top:35%;
}

#wrapper{
    background-color:pink;
}
#maps-toggler{
    position: relative;
    /* width:100%;
    height:100%;  */
}
#maps-wrapper{
    z-index:1;
   
}
.leaflet-container{
    position:absolute;
    width:100%;
    height:100%;
    border:1px solid#c7c3c3;
}
.leaflet-marker-icon{
    opacity:0.6;
    background-color:transparent;
}
#map-toggler {
    display: flex;
    align-self: flex-start;
    position:absolute;
    bottom:-20px;
}
.toggler {
    background-color: #666;
    border-left: 1px solid#444;
    border-right: 1px solid#444;
    border-bottom: 3px solid#444;
    margin-right: .5em;
    z-index: 2;
    font-size: .7em;
    padding: 1px 4px;
    cursor:pointer;
}
/* wrapper */

#list-infected-wrapper{
    display:flex;
    flex-direction:column;
    align-items:center;
    max-width:100%;
    border:1px solid#444;
    border-radius:3px;
    background-color:#333;
    position:relative;
}

/* heading */
#heading{
    text-align:center;
    line-height:.8em;
    width:100%;
    padding:.7em 0;
    border-bottom:1px solid#444;
}
.infected-num{
    font-family:"Inconsolata";
    color:#DE354C;
}
.num{
    font-size:2.2em;
}

/* ul heading */
#admin0-heading{
    padding:.7em;
    font-size:.9em;
}
#admin2-heading{
    padding:.5em;
    font-size:.9em;
    display:flex;
    flex-direction:column;
    align-items:center;
}

/* ul */
#ul{
    width:100%;
    overflow-y:auto;
}
#ul::-webkit-scrollbar-track
{
	background-color: #444;
}

#ul::-webkit-scrollbar
{
	width: 10px;
	background-color: #F5F5F5;
}

#ul::-webkit-scrollbar-thumb
{
	background-color: #888;
}
.li{
    border-bottom:1px solid#444;
    padding:.7em 0 .7em .5em;
}
.li-provinces{
    display:flex;
    flex-direction:column;
    border-bottom:1px solid#444;
    padding:.5em 0 .5em .5em;
}
.suffix{
    font-size:0.9em;
    color: #f5f5f5;
}
.li .confirmed-num{
    margin-right:.1em;
} 
#card-toggler {
    display: flex;
    align-self: flex-start;
    position:absolute;
    bottom:-2px;
}

#list-deaths-wrapper{
    display:flex;
    flex-direction:column;
    align-items:center;
    max-width:100%;
    border:1px solid#444;
    border-radius:3px;
    background-color:#333;
    position:relative;
}

/* ul */
.li .deaths-num{
    margin-right:.1em;
}
.deaths-num{
    font-family:"Inconsolata";
    color:#999;
}
.recovered-num{
    font-family:"Inconsolata";
    color:#4D774E;
}

/* wrapper */
#list-active-wrapper{
    display:flex;
    flex-direction:column;
    align-items:center;
    max-width:100%;
    border:1px solid#444;
    border-radius:3px;
    background-color:#333;
    position:relative;
}

/* heading */
#heading-active{
    text-align:center;
    line-height:.8em;
    width:100%;
    padding:.3em 0;
    border-bottom:1px solid#444;
}
#incidentRate-heading{
    line-height:.1em;
}
.active-num{
    font-family:"Inconsolata";
    color:#F1B24A;
}
.incidentRate-num{
    font-family:"Inconsolata";
    color:#FFDF6C;
}

/* list */
.li .active-num{
    margin-right:.2em;
}
/* app wrapper */
#app-wrapper {
    background-color: #222;
    width: 100%;
    height: 100vh;
    color: #f5f5f5;
    margin:0;
    font-family:"Work Sans";
}

/* header */
#header {
    width: 100%;
    height: 50px;
    position: fixed;
    display: flex;
    align-items: center;
    z-index: 2;
    background-color: #240090;
}
#icon-covid{
    height: 100%;
    background-color:  #3500D3;
}
#header h1 {
    margin: 0 .5em;
    font-size: 1.7em;
    font-weight:300;
}
#header p {
    display: flex;
    flex-direction: column;
    font-weight:500;
    border-left: 1px solid white;
    padding-left:.7em;
}
#nav {
    margin-left: auto;
    order: 1;
    font-size: 2em;
}
#nav a {
    margin-right: 1em;
    text-decoration: none;
    color: white;
}
#main{
    position:relative;
    top:3.7em;
}

/* map and toggler cards*/
/* map */
#cards-map-wrapper {
    display: flex;
    justify-content: space-evenly;
}

/* cards */
.admin-icon{
    z-index:2;
    font-size:1.7em;
    color:#3500D3;
    cursor:pointer;
}
.card-toggler{
    display:flex;
    justify-content:space-between;
    align-items:center;
    margin:0 1em;
    background-color:#333;
    border-left: 1px solid#444;
    border-right: 1px solid#444;
    border-bottom: 1px solid#444;
}
.card-toggler-name{
    font-size:.7em;
}

/* country picker and charts */
#charts{
    display:flex;
    margin-top:-1.7em;
}
#country-chart{
    background-color:#333;
    align-items:center;
    border: 1px solid #444;
    width:775px;
    height:307px;
    max-height:100%;
    max-width:100%;
    margin-right:.2em;
    position:relative;
}
/* charts and infos */
/* num of country and lastupdated */
.charts-infos{
    display:flex;
    justify-content:space-evenly;
}
#info-panel{
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    margin-top:5px;
}
.row-panel-wrapper{
    border:1px solid#444;
    background-color:#333;
    display:flex;
    justify-content:space-evenly;
    position:relative;
}
.today-data{
    text-align:center;
    line-height:0.4em;
}
.today-data h2{
    font-family:"Inconsolata";
}
.today-infected-panel{
    border-right:1px solid#444;
    border-bottom:1px solid#444;
}
.num-of-country-panel{
    border-top:1px solid#444;
    border-left:1px solid#444;
    margin-left:-1px;
}
.row-panel-wrapper span{
    font-size:.6em;
}
.update-panel{
    border:1px solid#444;
    background-color:#333;
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center;
    line-height:.4em;
    padding:1.2em 0 .6em 0;
    position:relative;
}

/* expand and hide */
.hide{
    display:none;
}
.expand{
    width:100%;
}
.expand-shrink-icon{
    opacity:.8;
    position:absolute;
    font-size:15px;
    z-index:3;
}

/* loader */
.loader-wrapper{
    display:flex;
    flex-direction:column;
    align-items:center;
    margin-top:25%;
}
.loading{
    color:white;
}
